 67%; /* aaaaaa */\n  --clr-dark-grey: 0, 0%, 38%; /* 606060 */\n  --clr-white: 0, 0%, 89%; /* e4e4e4 */\n\n  /* Padding */\n  --site-padding: 14vw;\n  --site-padding-large: 20vw;\n}\n\n/* Utility classes */\n.bg-black {\n  background-color: hsl(var(--clr-black));\n}\n.bg-green-blue {\n  background-color: hsl(var(--clr-green-blue));\n}\n\n/* .spaced > *:not(:last-child) {\n  margin-bottom: 1.5rem;\n} */\n\nbody {\n  font-family: \"Raleway\", sans-serif;\n}\n\n.container-fluid {\n  padding: 0;\n}\n\n.section-container {\n  padding-block: 2rem;\n  padding-inline: 1rem;\n  height: auto;\n}\n\n@media only screen and (min-width: 992px) {\n  .section-container {\n    padding-block: 3rem;\n    padding-inline: var(--site-padding);\n  }\n}\n\n@media only screen and (min-width: 1500px) {\n  .section-container {\n    padding-block: 5rem;\n    padding-inline: var(--site-padding-large);\n  }\n}\n\n/* ---------------- */\n/* ---Herobanner--- */\n/* ---------------- */\n.hero-banner {\n  height: 100vh;\n  padding-top: 25vh;\n  background: rgb(247, 245, 212);\n  background: radial-gradient(\n    circle,\n    rgba(247, 245, 212, 1) 0%,\n    rgba(250, 234, 153, 1) 29%,\n    rgba(213, 131, 69, 1) 61%,\n    rgba(60, 39, 54, 1) 100%\n  );\n}\n\n.hero-banner-content > * {\n  margin-bottom: 1rem;\n}\n\nh1 {\n  margin-bottom: 0.5rem;\n}\n#hero-banner__subtitle {\n  padding-inline: 1.5rem;\n  padding-block: 0.2rem;\n  font-style: italic;\n}\n.title2 img {\n  margin-inline: 0.5rem;\n}\n.title-deco {\n  height: 1rem;\n}\n.deco__line {\n  display: none;\n}\n\n.hero-banner-arrow {\n  font-size: 2rem;\n  color: hsl(var(--clr-green-blue));\n}\n\n/* Large devices (desktops, 992px and up) */\n@media only screen and (min-width: 992px) {\n  .hero-banner {\n    padding-inline: var(--site-padding);\n    padding-top: 40vh;\n  }\n  .hero-banner-content {\n    width: fit-content;\n  }\n\n  #hero-banner__subtitle {\n    display: inline-block;\n    width: 100%;\n    text-align: left;\n    padding-inline: 0.5rem;\n  }\n\n  .deco__line {\n    display: block;\n    height: 0.3rem;\n    width: 12.5%;\n    border-top: 1px solid hsl(var(--clr-white));\n    border-bottom: 1px solid hsl(var(--clr-white));\n    margin-inline: 0;\n  }\n\n  .hero-banner-arrow {\n    font-size: 2.5rem;\n    cursor: pointer;\n  }\n\n  .hero-banner-content i {\n    width: fit-content;\n    margin: auto;\n  }\n}\n\n@media only screen and (min-width: 1500px) {\n  .hero-banner {\n    padding-inline: var(--site-padding-large);\n    padding-top: 45vh;\n  }\n}\n\n/* -------------- */\n/* --- Header --- */\n/* -------------- */\n.header {\n  position: relative;\n  border-top: 1px solid hsl(var(--clr-light-grey));\n  border-bottom: 1px solid hsl(var(--clr-light-grey));\n  padding-inline: 1.5rem;\n}\n.logo {\n  height: 3rem;\n  margin: 0.5rem;\n}\n.menu-close-icon,\n.menu-open-icon {\n  font-size: 2.5rem;\n  color: hsl(var(--clr-dark-grey));\n  cursor: pointer;\n  transition: all 0.3s;\n}\n.menu-close-icon:hover,\n.menu-open-icon:hover {\n  color: hsl(var(--clr-green-blue));\n}\n.menu-close-icon {\n  display: none;\n}\n.menu-close {\n  display: none;\n}\n\n/* Navbar */\n.navbar {\n  display: block;\n  visibility: hidden;\n  opacity: 0;\n  width: 95%;\n  text-align: center;\n  position: absolute;\n  top: 5rem;\n  left: 50%;\n  transform: translate(-50%, 0);\n  background-color: hsl(var(--clr-white));\n  z-index: 999;\n  border-radius: 0.3rem;\n  padding: 1rem;\n}\n\n.menu-open {\n  animation: 0.5s fadeIn;\n  animation-fill-mode: forwards;\n}\n\n.menu-close {\n  animation: 0.5s fadeOut;\n  animation-fill-mode: forwards;\n}\n.navbar li:not(:last-child) {\n  margin-bottom: 0.8rem;\n}\n.navbar li {\n  list-style-type: none;\n}\n.navbar li:hover * {\n  color: white;\n  text-decoration: none;\n  background-color: hsl(var(--clr-green-blue));\n}\n.navbar a {\n  font-size: 0.875rem;\n  font-weight: 800;\n  text-transform: uppercase;\n  color: hsl(var(--clr-dark-grey));\n  padding: 0.4rem;\n  border-radius: 0.2rem;\n}\